body {
  margin: 0;
  padding: 0;
  background-image: url("http://www.garotasgeeks.com/wp-content/uploads/2016/01/sailor-moon-wall-07.jpg");
  background-size: auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  justify-items: center;
}

.card {
    background: #fff;
    border-radius: 2px;
    height: 108px;
    margin: 1rem;
    position: relative;
    width: 108px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }

#img-tile {
    height: 100px;
    width: 100px;
    padding:4px;
}
.wrapper {
    background: lavender;
    height: 100%;
    width: 50%;
    display: flex;
    flex-flow: row wrap;
    margin: 0 auto;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
.jumbotron {
    background-color: lavender;
    min-height: 150px;
    font-family: 'Handlee', cursive;
    color: purple;
    font-size: 30px;
    text-align: center;
    margin-bottom: 50px;
}

h2 {
    padding-top: 30px;
    margin: 0;
}

h4 {
    margin: 0;
    font-style: italic;
}



.navbar {
    background-color: #ffedef;
    margin-bottom: 0;
    height: 50px;
}

ul {
    list-style-type: none;
    padding: 0;
    margin:0;
  }

  li {
      float: left;
      margin-top: 5px;
      color: purple;
  }

  #score-text {
      font-size: 25px;
  }

  #top-score-text{
      font-size: 25px;
  }

#nav-img{
    width: 5%;
    height: auto;
    margin-left: 5px;
    margin-top:5px;
    margin-right: 30px;
}

br {
    height: 1px;
    margin: 0;
}

p {
    font-size: 8px;
    margin-top:2px;
    margin-left:5px;
    font-family: 'Handlee', cursive;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

